import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { BrowserRouter as Route, } from "react-router-dom";
import AuthUserContext from './AuthUserContext';
import { AuthConsumer } from './withAuthorization';
import SignOut from './SignOut';


// import css
import './WelcomePage.css'

const NavigationAuth = () =>
    <div className="flex-box-center-container flex-horizontal-center">
        <Link className="link" to="/homepage"><div className="sign-btn to-home-page"> </div></Link><br />
        <Link className="link" to="/account"><div className="sign-btn to-account-page"> </div></Link><br />
        <Link className="link" to="/signout"><div className="sign-btn signout"></div></Link><br />
    </div>

const NavigationNonAuth = () =>
    <div className="flex-box-center-container flex-horizontal-center">
        <Link className="link" to="/signup" ><div className="sign-btn signup"> </div></Link>
        <br />
        <Link className="link" to="/signin"><div className="sign-btn signin"> </div></Link><br />
        <Link className="link" to="/gps"><div className="sign-btn visitor"> </div></Link><br/>
    </div>

const Navigation = () =>
    <AuthConsumer>
        {({isAuth}) =>(
             (isAuth === true )? 
             <NavigationAuth />
             : <NavigationNonAuth />) 
        }
    </AuthConsumer>

export default Navigation;
